
@import '../../style/variables.scss';

.app{
    width: 100%;
    height: 100%;
    opacity: 1;
    transition: opacity 550ms linear;
    &__anim{
        opacity: 0;
    }
    &__back{
        z-index: 1;
    }
    &__content{
        position: relative;
        z-index: 2;
    }
}
